<template lang="html">
  <div class="outer">
    homePage
    {{msg}}
    <router-link to='about'>about</router-link>
    <div class="none-height">
      <div class="test">
        <div v-infinite-scroll="loadMore" infinite-scroll-disabled="busy" infinite-scroll-distance="10">
          <div class="" v-for='item in data'>
            {{item.name}}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
var count = 0
export default {
  data () {
    return {
      msg: 'home page',
      data: [],
      busy: false
    }
  },
  methods: {
    loadMore: function () {
      this.busy = true
      setTimeout(() => {
        for (var i = 0, j = 3; i < j; i++) {
          this.data.push({name: count++})
        }
        this.busy = false
        console.log(this.data)
      }, 100)
    }
  }
}
</script>
<style media="screen">
  .outer {
    /*height: 800px;*/
    position: absolute;
    left: 0px;
    top: 0px;
    right: 0px;
    bottom: 0px;
    /*overflow: hidden;*/
    overflow: scroll;

    .none-height {
      .test {
        position: absolute;
        
      }
    }

  }
</style>



